import '../css/ProjectPage.css'; // Estilos para la página del proyecto
import { useTranslation } from "react-i18next";
import "../components/i18n";

export const AcmeAnsPage = () => {

    const { t, i18n } = useTranslation();
    // Lista de tecnologías que utilizaste
    const technologies = [
        "Java",
        "Framework Acme-Ans",
        "HTML",
        "MariaDB"
    ];

    // Array para las imágenes de la galería
    const images = [
        // { src: skullKingImage1, alt: "Vista del tablero de juego" },
        // { src: skullKingImage2, alt: "Ejemplo de una jugada" }
    ];

    

    return (
        <div className="project-page">
            <header className="project-page-header">
                <h1>Acme ANS</h1>
                <p>{t('acmeAnsIntro')} </p>
                <p>{t('acmeAnsIntro2')} </p>
            </header>

            <section className="project-section">
                <h2>{t('objTitle')}</h2>
                <div>
                    <p>{t('acmeAnsObj')}</p>
                </div>
            </section>

            <section className="project-section">
                <h2>{t('lectionsTitle')}</h2>
                <div>
                    {t("acmeAnsLections", { returnObjects: true }).map((lection, index) => (
                    <p key={index}>{lection}</p>
                ))}
                </div>
            </section>

            <section className="project-section">
                <h2>{t('techTitle')}</h2>
                <ul className="tech-list">
                    {technologies.map((tech, index) => (
                        <li key={index}>{tech}</li>
                    ))}
                </ul>
            </section>

            <section className="project-section">
                <h2>{t('linkRepo')}</h2>
                <div className="repo-link-container">
                    <a href="https://github.com/candeegtz/Acme-ANS" target="_blank" rel="noopener noreferrer" className="repo-link-button">
                        Acme ANS
                    </a>
                </div>
            </section>

            <section className="project-section">
                <h2>{t('conclusionTitle')}</h2>
                <div>
                    <p>{t('acmeAnsConclusion')}</p>
                </div>
            </section>
 
            <section className="project-section">
                <h2>{t('galleryTitle')}</h2>
                <div className="image-gallery">
                    <p>{t('proxImgs')}</p>
                </div>
            </section>
        </div>
    )
}

export default AcmeAnsPage;